			       currPage.data.isDoRefresh = false;
				   this.getInquiryInfo()
				  // this.getInquiryInfoPicture()
			 }
		 this.getInquiryInfo()
	},
    onLoad:function(options) {
		_this = this
    	_quotationNumber = options.quoteNumber
